import {useEffect, lazy, Suspense, useState, useContext} from 'react';
import { Route, Switch, useRouteMatch, useHistory, Redirect, Link } from 'react-router-dom';
import logo from './image/logo.svg'
import {useDispatch, useSelector} from 'react-redux';
import {getToken} from '../application/selectors/auth';
import { post, fetchData } from '../application/middlewares/index';
import {setCategory,setFilters} from "../application/actions/data";
import { getCategory,getFilters,getUser } from '../application/selectors/auth';
import Lang1 from '../pages/Language/Lang1';
import { LangContext } from "../pages/Language/LangContext";
import Input from "@material-tailwind/react/Input";
import Button from "@material-tailwind/react/Button";
import Flickity from 'react-flickity-component';
import { Icon } from 'react-icons-kit'
import {edit} from 'react-icons-kit/typicons/edit';
import {ic_person} from 'react-icons-kit/md/ic_person'
import {heart} from 'react-icons-kit/fa/heart'
import {ic_home} from 'react-icons-kit/md/ic_home'
import {ic_logout} from 'react-icons-kit/md/ic_logout'




const Navbar = lazy(() => import('./Navbar'));
const Update = lazy(() => import('./Update'));
const Footer = lazy(() => import('./Footer'));

function Profile(){
  const { lang, dispatchLang } = useContext(LangContext);

    const dispatch = useDispatch();
    const history = useHistory();
    const categories = useSelector(getCategory);
    const user = useSelector(getUser);
    const token = useSelector(getToken);
    // console.log(user);
    const [active, setActive] = useState(0);
    const [update_id, setUpdate_id] = useState();
    const [page_router, setPage_router] = useState(1);
    const [products, setProducts] = useState();
    const [wish, setWish] = useState();
    const flickityOptions = {
        // lazyLoad: true,
        pageDots: true,
        wrapAround: true,
        prevNextButtons:true,
        pageDots:true
    }
    // console.log(token)
       useEffect(()=>{
           if(token){
        dispatch(fetchData({
            url:`user/tm/get-wish-list`,
            token:token,
            action:(response)=>{
                if(response.success){
                    console.log("get_wishlist",response)
                    // toast.success("Halanlaryma goşuldy!");
                    // console.log(response.data.rows);
                    setWish(response.data.rows);
                } else {
                    console.log("error_data ",response);
                    }
                }
            }))
        }
    },[])

    // const [filter_id, setFilter_id]= useState({});
    let dil;
    if (lang=='ru') {dil='ru'} else {dil='tm'}
    // console.log(token,"----profile")
    useEffect(() => {
        if (token){
        dispatch(fetchData({
            url:`user/tm/user-real-estates`,
            token: token,
            action:(response)=>{
                if(response.success){
                    console.log("get_reject ",response);
                    // console.log("----",response.data.rows[0].main_types);
                    setProducts(response.data.rows);
                    // console.log(categories)
                } else {
                    console.log("error_data ",response);
                    }
                }
            }))
        }
    }, [dil]);
    useEffect(() => {
        var selects1 = document.querySelectorAll(".pro");
        // console.log(selects1[0]);
            // console.log(selects1[0]?.children[0]);
            let radio1 = selects1[0]?.children[0].setAttribute('checked', 'checked');
            let pro = selects1[0].classList.add("pro_active");
    }, []);
    
    
    return(
        <main>
            <Navbar />
            <div className="main bg-gray-100">
                <div className="main1 flex justify-between">
                    <div className="Pro_category border-r p-2 flex flex-col justify-between" style={{height:"500px"}}>
                        <div className="py-8 border-b flex items-center">
                            <div className="rounded-full h-14 w-14 bg-gray-400 flex justify-center items-center">
                        <Icon size={25} icon={ic_person} className="text-gray-200" />
                            </div>
                            <span className="text-sm ml-4">ID : {user.id}</span>
                        </div>
                        <div className="py-8">
                        <label onClick = {()=>{
                            setPage_router(1);
                            var selects = document.querySelectorAll(".pro");
                            selects.forEach(function(item) {
                            if(item.children[0].checked) {
                            let q= item.classList.add("pro_active");
                            } else {
                            let q= item.classList.remove("pro_active");
                            }
                        });
                        }} className="pro py-2 px-3 my-1 rounded-md hover:bg-gray-200 cursor-pointer flex items-center font-medium">
                        <input type="radio" className="hidden" name="profile"/> 
                        <Icon size={25} icon={ic_person} className="text-gray-600 mr-2" />
                         <span>Profile</span>
                        </label>
                        <Link to='/wishlist' className="pro py-2 px-3 my-1 rounded-md hover:bg-gray-200 cursor-pointer flex items-center font-medium">
                        <Icon size={20} icon={heart} className="text-gray-600 mr-2" />
                            <span>Wishlist</span><span className="rounded-full h-7 w-7 ml-4 text-sm text-white bg-gray-400 flex items-center justify-center">{wish?.count}</span>
                        </Link>
                        <label onClick = {()=>{
                            setPage_router(2);
                            var selects = document.querySelectorAll(".pro");
                            selects.forEach(function(item) {
                            if(item.children[0].checked) {
                            let q= item.classList.add("pro_active");
                            } else {
                            let q= item.classList.remove("pro_active");
                            }
                        });
                        }} className="pro py-2 px-3 my-1 rounded-md hover:bg-gray-200 cursor-pointer flex items-center font-medium">
                        <input type="radio" className="hidden" name="profile" /> 
                        <Icon size={25} icon={ic_home} className="text-gray-600 mr-2" />
                        <span>Emlakler</span><span className="rounded-full h-7 w-7 ml-4 text-sm text-white bg-gray-400 flex items-center justify-center">{products?.length}</span>
                        </label>
                        </div>
                        <div className="py-8 border-t font-medium">
                            <p className="py-2 px-3 my-1 rounded-md hover:bg-gray-200 cursor-pointer">
                        <Icon size={25} icon={ic_logout} className="text-gray-600 mr-2" />
                            <span>Log out</span>
                            </p>
                        </div>
                    </div>
                    {page_router==1 ?
                   <div className="profile_main flex bg-white border rounded-md my-6 p-3">
                        <div className="w-1/2">

                        <div className="w-64 my-4">
                            <Input 
                           type="text"  size="regular" color="blue"
                           outline={true} placeholder="Name" value={user?.full_name}  />
                        </div>
                        <div className="w-64 my-4">
                            <Input 
                           type="email"  size="regular" color="blue"
                           outline={true} placeholder="Email" value={user?.email} />
                        </div>
                        <div className="w-64 my-4">
                            <Input 
                           type="tel"  size="regular" color="blue"
                           outline={true} placeholder="Phone" value={user?.phone}  />
                        </div>
                        <div className="mt-6 w-full flex">
                            <Button
                                color="lightBlue"
                                buttonType="link"
                                size="regular"
                                rounded={false}
                                block={false}
                                iconOnly={false}
                                ripple="dark"
                            >
                                Save
                            </Button>
                        </div>
                        </div>
                        <div className="w-1/2">
                        <div className="w-64 my-4">
                            <Input 
                           type="password"  size="regular" color="blue"
                           outline={true} placeholder="Password"  />
                        </div>
                        <div className="w-64 my-4">
                            <Input 
                           type="password"  size="regular" color="blue"
                           outline={true} placeholder="New password"  />
                        </div>
                        <div className="mt-6 w-full flex">
                            <Button
                                color="lightBlue"
                                buttonType="link"
                                size="regular"
                                rounded={false}
                                block={false}
                                iconOnly={false}
                                ripple="dark"
                            >
                                Save
                            </Button>
                        </div>
                        </div>
                    </div>
                    :
                    page_router==2 ?
                    <div className="profile_main flex flex-col bg-white border rounded-md my-6 p-3">
                        <div className="products w-full flex flex-col">
                        {products?.map((item)=>(
                            <div key={item.id}>
                            <div  className="product_box flex justify-between w-full rounded-md p-5">
                                
                                {item.is_active == null ? 
                                <div className="status_user text-yellow-500 text-sm">Seredilyar ...</div>
                                :
                                null
                                }
                                {item.is_active == true ? 
                                <div className="status_user text-green-500 text-sm">Tassyklanyldy</div>
                                :
                                null
                                }
                                {item.is_active == false ? 
                                <div className="status_user text-red-500 text-sm">Reject</div>
                                :
                                null
                                }
                                <div onClick={()=> {
                                    setPage_router(3);
                                    setUpdate_id(item.id);
                                }}
                                className="edit shadow-lg cursor-pointer rounded-md bg-blue-100">
                                    <Icon size={24} icon={edit} className="text-blue-600 ml-1 mb-1" />
                                </div>
                            {/* <div className="heart1 text-gray-600 border rounded-md text-sm font-medium cursor-pointer hover:bg-gray-100"><Icon size={18} icon={heartO}/></div> */}
                            {/* <div className="location1 text-gray-600 border rounded-md text-sm font-medium cursor-pointer hover:bg-gray-100"><Icon size={20} icon={ic_room}/></div> */}
                            {/* <div className="time1 text-gray-500 text-sm ">{item.created_at}</div> */}
                                <div className="slider1">
                                <Flickity
                                className={'carousel1_user carousel-main'} // default ''
                                elementType={'div'} // default 'div'
                                options={flickityOptions} // takes flickity options {}
                                disableImagesLoaded={false} // default false
                                reloadOnUpdate // default false
                                static // default false
                                >
                                    {item?.images?.map((img)=>(
                                        <div className="carousel1_user-cell">
                                            <img src={`http://192.168.31.30:2000/${img?.destination}-big.webp`} className="img" />
                                        </div>
                                    ))}
                                </Flickity>
                                </div>
                                <div 
                                  
                                className="product_text flex flex-col items-between p-2">
                                    <div>
                                    <h1 className="text-2xl text-blue-600 font-medium pr-32">{item.real_estate_name}</h1>
                                    <h1 className="text-xl font-medium mt-3">{item.location}</h1>
                                    {/* 42 м² */}
                                    {/* <p className="text-sm text-gray-500 my-3">+993 {item.phone}</p> */}
                                    <h1 className="text-xl font-medium mb-5 my-3">{item.price} <span className="text-sm">TMT</span> </h1>
                                    </div>
                                    <p className="text-sm text-gray-500 pr-32">{item.description}</p>
                                </div>
                            </div>
                            <hr /> 
                            </div>
                        ))}
                        
                    </div>
                    </div>
                    :
                    <Update update_id={update_id} />              
                    }
                </div>
            </div>
            <Footer />
        </main>

    )
}

export default Profile;